#timeline h1{
  margin-bottom: 2.5rem;
  color: white;
}

.containers {
  min-height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 100px 0;
  background-color: #ab203d;
  flex-direction: column;
  cursor: none;
}
.timelines {
  width: 75%;
  height: auto;
  max-width: 800px;
  margin: 0 auto;
  position: relative;
}

.timelines ul {
  list-style: none;
}
.timelines ul li {
  padding: 20px;
  background-color: #1e1f22;
  /* background-color: #000; */
  color: white;
  border-radius: 10px;
  margin-bottom: 20px;
}
.timelines ul li:last-child {
  margin-bottom: 0;
}
.timeline-content h1 {
  font-weight: 500;
  font-size: 25px;
  line-height: 30px;
  margin-bottom: 10px;
}
.timeline-content p {
  font-size: 16px;
  line-height: 30px;
  font-weight: 300;
}
.timeline-content .date {
  font-size: 12px;
  font-weight: 300;
  margin-bottom: 10px;
  letter-spacing: 2px;
}

.timeline-box{
  transition: -webkit-transform 2s;
  transition: transform 2s;
  transition: transform 2s, -webkit-transform 2s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  cursor: pointer;
  -webkit-perspective: 1000px;
          perspective: 1000px;
}


@media only screen and (min-width: 768px) {
  .timelines:before {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 2px;
    height: 100%;
    background-color: #fff;
  }
  .timelines ul li {
    width: 50%;
    position: relative;
    margin-bottom: 50px;
  }
  .timelines ul li:nth-child(odd) {
    float: left;
    clear: right;
    -webkit-transform: translateX(-60px);
            transform: translateX(-60px);
    border-radius: 20px 0px 20px 20px;
  }
  .timelines ul li:nth-child(even) {
    float: right;
    clear: left;
    -webkit-transform: translateX(30px);
            transform: translateX(30px);
    border-radius: 0px 20px 20px 20px;
  }
  .timelines ul li::before {
    content: "";
    position: absolute;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background-color: white;
    top: 0px;
  }
  .timelines ul li:nth-child(odd)::before {
    -webkit-transform: translate(50%, -50%);
            transform: translate(50%, -50%);
    right: -28px;
  }
  .timelines ul li:nth-child(even)::before {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    left: -30px;
  }
  .timeline-content .date {
    position: absolute;
    top: -30px;
  }
  .timelines ul li:hover::before {
    background-color: black;
  }
}

